
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/src/renderer/ifDefined.js';
import { html, svg, repeat } from '@ui5/webcomponents-base/src/renderer/LitRenderer.js';
const TableRowLitRenderer = {};
const block0 = (context) => { return html`<div 	id="${ifDefined(context.ctr._id)}"	data-sap-ui="${ifDefined(context.ctr._id)}"	?aria-hidden="${ifDefined(context.ariaHidden)}"	class="${ifDefined(context.classes.main)}"	style="${ifDefined(context.styles.main)}"	tabindex="${ifDefined(context.ctr._tabIndex)}">	${ repeat(context.visibleCells, undefined, (item, index) => block1(item, index, context)) }${ repeat(context.popinCells, undefined, (item, index) => block2(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<div class="${ifDefined(context.classes.cellWrapper)}"><slot name="${ifDefined(item._individualSlot)}"></slot></div>	`; };
const block2 = (item, index, context) => { return html`<div class="${ifDefined(context.classes.popin)}"			style="grid-column-end: ${ifDefined(context.visibleColumnLength)}" ><span class="${ifDefined(context.classes.popinTitle)}">${ifDefined(item.popinText)}</span><div><slot name="${ifDefined(item.cell._individualSlot)}"></slot></div></div>	`; };
const renderMe = block0;
TableRowLitRenderer.render = renderMe;
export default TableRowLitRenderer;