
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/src/renderer/ifDefined.js';
import { html, svg, repeat } from '@ui5/webcomponents-base/src/renderer/LitRenderer.js';
const TableLitRenderer = {};
const block0 = (context) => { return html`<div 	id="${ifDefined(context.ctr._id)}"	data-sap-ui="${ifDefined(context.ctr._id)}"	?aria-hidden="${ifDefined(context.ariaHidden)}"	style="display: grid; place-items: center;"><!-- columns --><div class="${ifDefined(context.classes.main)}" style="${ifDefined(context.styles.main)}">		${ repeat(context.visibleColumns, undefined, (item, index) => block1(item, index, context)) }</div><!-- rows -->	${ repeat(context.ctr.rows, undefined, (item, index) => block2(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<div class="sapWCTableColumnWrapper"><slot name="${ifDefined(item._individualSlot)}"></slot></div>		`; };
const block2 = (item, index, context) => { return html`<div style="width: 100%"		><slot name="${ifDefined(item._individualSlot)}"></slot></div>	`; };
const renderMe = block0;
TableLitRenderer.render = renderMe;
export default TableLitRenderer;