
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/src/renderer/ifDefined.js';
import { html, svg, repeat } from '@ui5/webcomponents-base/src/renderer/LitRenderer.js';
const YearPickerLitRenderer = {};
const block0 = (context) => { return html`<div 	id="${ifDefined(context.ctr._id)}"	data-sap-ui="${ifDefined(context.ctr._id)}"	?aria-hidden="${ifDefined(context.ariaHidden)}"	class="sapWCYearPicker"	role="grid"	aria-readonly="false"	aria-multiselectable="false"	style="${ifDefined(context.styles.main)}">	${ repeat(context.ctr._yearIntervals, undefined, (item, index) => block1(item, index, context)) }</div>`; };
const block1 = (item, index, context) => { return html`<div class="${ifDefined(context.classes.yearInterval)}">			${ repeat(item, undefined, (item, index) => block2(item, index, context)) }</div>	`; };
const block2 = (item, index, context) => { return html`<div id="${ifDefined(item.id)}"					tabindex="${ifDefined(item._tabIndex)}"					data-sap-timestamp="${ifDefined(item.timestamp)}"					class="${ifDefined(item.classes)}"					role="gridcell"					aria-selected="false">						${ifDefined(item.year)}</div>			`; };
const renderMe = block0;
YearPickerLitRenderer.render = renderMe;
export default YearPickerLitRenderer;