import { html, svg, repeat } from '@ui5/webcomponents-base/src/renderer/LitRenderer.js';
const DayPickerLitRenderer = {};
const block0 = (context) => { return html`<div 	id="${ifDefined(context.ctr._id)}"	data-sap-ui="${ifDefined(context.ctr._id)}"	?aria-hidden="${ifDefined(context.ariaHidden)}" class="${ifDefined(context.classes.wrapper)}" style="${ifDefined(context.styles.wrapper)}"><div class="${ifDefined(context.classes.weekNumberContainer)}">		${ repeat(context.ctr._weekNumbers, undefined, (item, index) => block1(item, index, context)) }</div><div id="${ifDefined(context.ctr._id)}-content" class="${ifDefined(context.classes.content)}"><div role="row" class="${ifDefined(context.classes.weekDaysContainer)}">			${ repeat(context.ctr._dayNames, undefined, (item, index) => block2(item, index, context)) }</div><div id="${ifDefined(context.ctr._id)}-days" class="sapWCDayPickerItemsContainer" tabindex="-1">			${ repeat(context.ctr._weeks, undefined, (item, index) => block3(item, index, context)) }</div></div></div>`; };
const block1 = (item, index, context) => { return html`<div class="sapWCDayPickerWeekNameContainer"><span class="sapWCDayPickerWeekName">${ifDefined(item)}</span></div>		`; };
const block2 = (item, index, context) => { return html`<div					id=${ifDefined(item._id)}					role="columnheader"					aria-label="${ifDefined(item.name)}"					class="${ifDefined(item.classes)}">					${ifDefined(item.ultraShortName)}</div>			`; };
const block3 = (item, index, context) => { return html`${ item.length ? block4(item, index, context) : block6(item, index, context) }`; };
const block4 = (item, index, context) => { return html`<div style="display: flex;">						${ repeat(item, undefined, (item, index) => block5(item, index, context)) }</div>				`; };
const block5 = (item, index, context) => { return html`<div								id="${ifDefined(item.id)}"								tabindex="${ifDefined(item._tabIndex)}"								data-sap-timestamp="${ifDefined(item.timestamp)}"								data-sap-index="${ifDefined(item._index)}"								role="gridcell"								aria-selected="${ifDefined(item.selected)}"								class="${ifDefined(item.classes)}"><span 										class="sapWCDayPickerDayText"										data-sap-timestamp="${ifDefined(item.timestamp)}"										data-sap-index="${ifDefined(item._index)}">											${ifDefined(item.iDay)}</span></div>						`; };
const block6 = (item, index, context) => { return html`<div class="sapWCEmptyWeek"></div>				`; };
